.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}
.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}
.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    color: $blanco;
    font-weight: $black;
  }
  p {
    font-size: 1.6rem;
    color: $blanco;
  }
}
